<template>
  <v-app-bar app dark color="success">
    <v-btn icon @click="setDrawer(!drawer)">
      <v-icon v-if="drawer">mdi-menu</v-icon>
      <v-icon v-else>mdi-view-dashboard</v-icon>
    </v-btn>
    <v-toolbar-title>后台管理</v-toolbar-title>
    <!-- v-spacer 用于占去剩余的所有空间，其前后的组件将会被挤到两侧 -->
    <v-spacer></v-spacer>
    <v-btn text class="ml-3" to="/">首页</v-btn>
    <v-btn text class="ml-3" to="/account">账号管理</v-btn>

    <user-status />
  </v-app-bar>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import AccountService from "@/service/account_service";

export default {
  name: "dashboard-app-bar",
  data: () => ({
    // currentUser: "",
  }),
  components: {
    UserStatus: () => import("@/components/UserStatus.vue"),
  },
  mounted() {
    console.log(this.$store.state.currentUser);
  },

  methods: {
    /// ... 在此处为解构操作，用于将mapMutations中的对象展开
    ...mapMutations(
      // 把 状态中定义的方法名："SET_DRAWER" 映射为当前组件的方法： setDrawer，
      ["SET_DRAWER", "SET_CURRENT_USER"]
    ),

    logout() {
      AccountService.logOut()
        .then((resp) => {
          this.SET_CURRENT_USER(null);
          if (this.$route.path != "/") {
            this.$router.push("/");
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  computed: {
    // 将状态中定义的'drawer' 映射为同名的计算属性,以下为等价的写法
    //
    // drawer: {
    //   get() {
    //     return this.$store.state.drawer;
    //   },
    //   set(val) {
    //     this.$store.commit("SET_DRAWER", val);
    //   },
    // },
    ...mapState(["drawer", "currentUser"]), // ... 在此处为解构操作， 用于展开 [] 内指定的对象
  },
};
</script>

